.cubespinner {
  animation-name: spincube;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
  animation-duration: 12s;
  transform-style: preserve-3d;
  transform-origin: 60px 60px 0; }

.cubespinner div {
  position: absolute;
  width: 150px;
  height: 150px;
  border: 1px solid #8d8d8d;
  background: rgba(8, 253, 216, 0.8);
  box-shadow: inset -10px 10px 40px #181818;
  line-height: 150px;
  text-align: center;
  font-size: 100px;
  backface-visibility: hidden; }

.cubespinner .face1 {
  transform: translateZ(75px); }

.cubespinner .face2 {
  transform: rotateY(90deg) translateZ(75px); }

.cubespinner .face3 {
  transform: rotateY(90deg) rotateX(90deg) translateZ(75px); }

.cubespinner .face4 {
  transform: rotateY(180deg) rotateZ(90deg) translateZ(75px); }

.cubespinner .face5 {
  transform: rotateY(-90deg) rotateZ(90deg) translateZ(75px); }

.cubespinner .face6 {
  transform: rotateX(-90deg) translateZ(75px); }

